/*------------------------------------*\
    CHECKBOXES
\*------------------------------------*/
.ds_checkbox {
  margin-bottom: 24px;
  padding-left: 40px;
  position: relative;
}
.ds_checkbox__input {
  height: 44px;
  left: -2px;
  opacity: 0;
  position: absolute;
  top: -2px;
  width: 44px;
}
.ds_checkbox__label {
  display: inline-block;
  padding: 4px 32px 4px 16px;
}
.ds_checkbox__label::before {
  background-color: #ffffff;
  border: 2px solid #333333;
  content: "";
  height: 40px;
  left: 0;
  position: absolute;
  width: 40px;
}
.ds_checkbox__label::after {
  content: "";
  position: absolute;
  bottom: calc(100% - 40px);
  left: 0;
  right: calc(100% - 40px);
  opacity: 0;
}
.ds_checkbox__label::before, .ds_checkbox__label::after {
  top: -4px;
}
@media (min-width: 768px) {
  .ds_checkbox__label::before, .ds_checkbox__label::after {
    top: 0;
  }
}
.ds_checkbox__input:focus + .ds_checkbox__label::before {
  border-width: 4px;
  box-shadow: 0 0 0 3px #fdd522;
}
@media screen and (forced-colors: active) and (prefers-contrast: more) {
  .ds_checkbox__input:focus + .ds_checkbox__label::before {
