/*------------------------------------*\
    PAGE SETUP
    anchor footer to bottom of the viewport on short pages
\*------------------------------------*/
html {
  min-height: 100%;
  position: relative;
}

.ds_page {
  min-height: calc(100vh - 360px);
  margin-bottom: 360px;
}

.ds_site-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

@supports (display: grid) {
  .ds_page {
    display: grid;
    margin-bottom: 0;
    min-height: 100vh;
    grid-template: "page-top" auto "page-middle" 1fr "page-bottom" auto;
  }
  .ds_page__top {
    grid-area: page-top;
  }
  .ds_page__middle {
    grid-area: page-middle;
  }
  .ds_page__bottom {
    grid-area: page-bottom;
  }
  .ds_site-footer {
    position: static;
  }
}
.ds_page__middle {
  display: grid;
  grid-template-rows: auto 1fr;
  padding-bottom: 4rem;
  position: relative;
}

/*------------------------------------*\
    PRE FOOTER BACKGROUND
    for when a block immediately preceding the footer has a non-white background
\*------------------------------------*/
.ds_page__middle:has(.ds_pre-footer-background) {
  padding-bottom: 0;
}

.ds_page__middle.js-pre-footer-background {
  padding-bottom: 0;
}

.ds_pre-footer-background {
  padding-bottom: 4rem;
}

/*------------------------------------*\
    CATEGORY PAGE LAYOUT
\*------------------------------------*/
.category-lower,
.ds_category-lower {
  background: #f8f8f8;
  border-top: 1px solid #ebebeb;
  margin-top: -1px;
}

/*------------------------------------*\
    STACK
    Your classic lobotomised owl https://alistapart.com/article/axiomatic-css-and-lobotomized-owls/
\*------------------------------------*/
.ds_stack > * {
  margin-bottom: 0;
}
.ds_stack > * + * {
  margin-bottom: 0;
  margin-top: 16px;
}

/*------------------------------------*\
    WRAPPERS
\*------------------------------------*/
.ds_wrapper {
  margin: 0 auto;
  padding: 0 16px;
  width: 100%;
}
.ds_wrapper--no-overflow {
  overflow: hidden;
}

@media (min-width: 768px) {
  .ds_wrapper {
    width: 768px;
  }
}
@media (min-width: 992px) {
  .ds_wrapper {
    width: 960px;
  }
}
@media (min-width: 1200px) {
  .ds_wrapper {
    width: 1152px;
  }
}
/*------------------------------------*\
    FIELD GROUP
\*------------------------------------*/
.ds_field-group {
  margin-top: 16px;
}
.ds_field-group--inline {
  display: flex;
  flex-direction: row;
}
.ds_field-group--inline > div:not(:last-child) {
  margin-right: 16px;
}

/*------------------------------------*\
    BREAKPOINT CHECK
\*------------------------------------*/
.ds_breakpoint-check {
  display: none;
}
@media (min-width: 480px) {
  .ds_breakpoint-check.ds_breakpoint-check--small {
    display: block;
  }
}
@media (min-width: 768px) {
  .ds_breakpoint-check.ds_breakpoint-check--medium {
    display: block;
  }
}
@media (min-width: 992px) {
  .ds_breakpoint-check.ds_breakpoint-check--large {
    display: block;
  }
}
@media (min-width: 1200px) {
  .ds_breakpoint-check.ds_breakpoint-check--xlarge {
    display: block;
  }
}

/*------------------------------------*\
    VISIBILITY
\*------------------------------------*/
.visually-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
}

.fully-hidden {
  display: none !important;
}

