}
.ds_checkbox__label {
  display: inline-block;
  padding: 4px 32px 4px 16px;
}
.ds_checkbox__label::before {
  background-color: #ffffff;
  border: 2px solid #333333;
  content: "";
  height: 40px;
  left: 0;
  position: absolute;
  width: 40px;
}
.ds_checkbox__label::after {
  content: "";
  position: absolute;
  bottom: calc(100% - 40px);
  left: 0;
  right: calc(100% - 40px);
  opacity: 0;
}
