@supports (display: grid) {
  /**
   * GRID KEY
   * c: content
   * f: feedback
   * h: header
   * l: list (e.g. category items, search results)
   * g: grid
   * n: navigation (e.g. subpages of a publication or guide)
   * p: partner branding
   * s: sidebar
   */
  body .ds_layout > *[class] {
    margin-left: 0;
    margin-right: 0;
    width: auto;
  }
  .ds_layout {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto 1fr;
    grid-template-areas: "h h" "p p" "c c" "s s" "f f";
    margin-right: 0;
    margin-bottom: 0;
  }
  .ds_layout__content {
    grid-area: c;
  }
  .ds_layout__feedback {
    grid-area: f;
  }
  .ds_layout__header {
    grid-area: h;
  }
  .ds_layout__list {
    grid-area: l;
  }
  .ds_layout__grid {
    grid-area: g;
  }
  .ds_layout__navigation {
    grid-area: n;
  }
  .ds_layout__partner {
    grid-area: p;
    align-self: start;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
  }
  @media (min-width: 768px) {
    .ds_layout__partner {
      margin-top: 2rem;
    }
  }
  @media (min-width: 768px) {
    .ds_layout__partner {
      margin-bottom: 2rem;
    }
  }
  .ds_layout__sidebar {
    grid-area: s;
  }
  .ds_layout--category-list {
    grid-template-areas: "h h" "c c" "l l" "g g" "f f";
  }
  .ds_layout--guide {
    grid-template-areas: "h h" "p p" "n n" "c c" "s s" "f f";
  }
  .ds_layout--search-results {
    grid-template-areas: "h h" "n n" "c c";
  }
  .ds_layout--search-results-with-sidebar {
    grid-template-areas: "h h" "n n" "s s" "c c";
