.category-lower,
.ds_category-lower {
  background: #f8f8f8;
  border-top: 1px solid #ebebeb;
  margin-top: -1px;
}

/*------------------------------------*\
    STACK
    Your classic lobotomised owl https://alistapart.com/article/axiomatic-css-and-lobotomized-owls/
\*------------------------------------*/
.ds_stack > * {
  margin-bottom: 0;
}
.ds_stack > * + * {
  margin-bottom: 0;
  margin-top: 16px;
}

/*------------------------------------*\
    WRAPPERS
\*------------------------------------*/
.ds_wrapper {
  margin: 0 auto;
  padding: 0 16px;
  width: 100%;
}
.ds_wrapper--no-overflow {
  overflow: hidden;
}

@media (min-width: 768px) {
  .ds_wrapper {
    width: 768px;
  }
}
@media (min-width: 992px) {
  .ds_wrapper {
    width: 960px;
  }
}
@media (min-width: 1200px) {
  .ds_wrapper {
    width: 1152px;
  }
}
/*------------------------------------*\
    FIELD GROUP
\*------------------------------------*/
.ds_field-group {
  margin-top: 16px;
}
.ds_field-group--inline {
  display: flex;
  flex-direction: row;
}
.ds_field-group--inline > div:not(:last-child) {
  margin-right: 16px;
}

/*------------------------------------*\
    BREAKPOINT CHECK
\*------------------------------------*/
.ds_breakpoint-check {
  display: none;
}
@media (min-width: 480px) {
  .ds_breakpoint-check.ds_breakpoint-check--small {
    display: block;
  }
}
@media (min-width: 768px) {
  .ds_breakpoint-check.ds_breakpoint-check--medium {
    display: block;
  }
}
@media (min-width: 992px) {
  .ds_breakpoint-check.ds_breakpoint-check--large {
    display: block;
  }
}
@media (min-width: 1200px) {
  .ds_breakpoint-check.ds_breakpoint-check--xlarge {
    display: block;
  }
}

/*------------------------------------*\
    VISIBILITY
\*------------------------------------*/
.visually-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
}

.fully-hidden {
  display: none !important;
}

/*------------------------------------*\
    LISTS
\*------------------------------------*/
.ds_no-bullets {
  list-style-type: none;
  margin-left: 0;
}

/*------------------------------------*\
    MARGIN MODIFIERS
    Deprecated, prefer use of margin override classes e.g. ds_!_margin--0, ds_!_margin-bottom--0
