/*------------------------------------*\
    #INSET TEXT
\*------------------------------------*/

.ds_inset-text {
  border-left: 8px solid $ds_colour__border;
  padding: 16px;

  @include ds_block-margins;
  @include ds_last-child-no-margin;
}

.ds_inset-text__text {
  @include ds_last-child-no-margin;
}

@include ds_media-query(medium) {
  .ds_inset-text {
      padding-left: 32px;
      padding-right: 64px;
  }
}
